// Abstracts
@use '../abstracts' as *;

/* ======== */
/* Preloader CSS */
/* ======== */

$fill:0;
:root {
    --width-fill: #{$fill};
}

.preloader{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    z-index: 999;
    svg{
        height:100%;
        width: 100%;
        path{
          fill:ss-var($colors,neutral,'neutral100');
        }
    }
    h2 {
        position: relative;
        -webkit-text-stroke: rem(1) ss-var($colors,neutral,"neutral0");
        background-clip: text;
        -webkit-background-clip: text;
        font-weight: ss-var($font-weight,blackbold);
        color: transparent;
        span{
            display: inline-flex;
            overflow: hidden;
            position: relative;

            &::before {
                content: attr(data-title);
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                width: var(--width-fill);
                background: ss-var($colors,neutral,"neutral0");
                -webkit-background-clip: text;
                background-clip: text;
                color: transparent;
                transition: all ease 0.1s;
                pointer-events: none; 
            }
        }
    
        
    }
    
}